//Elementos
const txt_segundos = document.querySelector("#seconds");
const txt_minutos = document.querySelector("#minutes");
const txt_horas = document.querySelector("#hours");
const txt_days = document.querySelector("#day");

document.addEventListener("DOMContentLoaded", function () {

    //Obtengo el año actual 
    const date = new Date();
    const añoActual = date.getFullYear();

    //Determino si es un año bisiesto o no
    var bisiesto = false;
    if (añoActual % 400 == 0 || (añoActual % 100 != 0 && añoActual % 4 == 0)) {
        bisiesto = true;
    }

    setInterval(() => {
        iniciarApp(añoActual, bisiesto);
    }, 1000);

});

function iniciarApp(añoActual, bisiesto) {
    //Obtengo la fecha con Mes, dia, año y horas
    var fecha = JSON.stringify(moment().format('MMMM Do YYYY, h:mm:ss a'));

    //Creo el arreglo con: Mes - Dia - Año - Hora
    const arregloFecha = fecha.split(" ");

    //Obteniendo mes sin comilla y transformando de nombre a numero
    const mes = obtenerMes(arregloFecha[0].replace('"', ""));       // Ej: December  → 12;

    //Obteniendo dia actual
    const dia = arregloFecha[1].slice(0, 2);

    //Obtener fecha actual formateada
    var fechaActual;
    if (dia.length == 1) {
        fechaActual = crearFecha(añoActual, mes, dia.padStart(2, "0"));
    } else {
        fechaActual = crearFecha(añoActual, mes, dia);
    }

    //Obtengo los dias de diferencia
    var finDeAño = moment(`${añoActual}-12-31`);
    const dias_Diferencia = finDeAño.diff(fechaActual, 'hours') / 24;

    //Obteniendo hora
    const arregloHora = arregloFecha[3].split(":");
    const horas = arregloHora[0];
    const minutos = arregloHora[1];
    const segundos = arregloHora[2];

    //Obtener hora en formato 24 horas
    if (arregloFecha[4] == 'pm"') {
        const horaPm = convertirPm(horas);
        imprimirHora(dias_Diferencia, horaPm, minutos, segundos);
    } else {
        imprimirHora(dias_Diferencia, Number.parseInt(horas), minutos, segundos);
    }

}

function obtenerMes(mes) {
    switch (mes) {
        case "January": return 1;
        case "February": return 2;
        case "March": return 3;
        case "April": return 4;
        case "May": return 5;
        case "June": return 6;
        case "July": return 7;
        case "August": return 8;
        case "September": return 9;
        case "Octuber": return 10;
        case "November": return 11;
        case "December": return 12;
    }
}

function crearFecha(año, mes, dia) {
    return moment(`${año}-${mes}-${dia}`);
}

function convertirPm(hora) {
    switch (hora) {
        case "1": return 13;
        case "2": return 14;
        case "3": return 15;
        case "4": return 16;
        case "5": return 17;
        case "6": return 18;
        case "7": return 19;
        case "8": return 20;
        case "9": return 21;
        case "10": return 22;
        case "11": return 23;
    }
}

function imprimirHora(dias, hora, minutos, segundos) {
    txt_days.textContent = ""+(dias)+"";
    txt_horas.textContent = "" + (23 - hora) + "";
    txt_minutos.textContent = "" + (59 - minutos) + "";
    txt_segundos.textContent = "" + (59 - segundos) + "";
}